@charset "UTF-8";
.ant-carousel .slick-slide {
  text-align: center;
  height: 230px;
  line-height: 230px;
  background: #364d79;
  overflow: hidden; }

.ant-carousel .slick-slide h3 {
  color: #fff; }

.ant-carousel .carousel-img {
  width: 100%;
  height: 230px; }

.main-article {
  display: flex;
  flex-direction: column;
  height: 100%; }
  .main-article .article-summary {
    width: 100%;
    height: 250px;
    box-sizing: border-box;
    display: flex;
    padding: 10px 0; }
    .main-article .article-summary .carousel {
      width: 60%;
      padding-right: 5px; }
    .main-article .article-summary .information {
      width: 40%;
      padding: 40px;
      color: #ffffff;
      background-image: linear-gradient(to right, #001529, #598bbb); }
      .main-article .article-summary .information h2 {
        color: #ffffff; }
      .main-article .article-summary .information .email {
        margin-top: 30px; }
        .main-article .article-summary .information .email span {
          display: block; }
  .main-article .article-layout {
    padding: 24px 20px;
    background: #fff;
    /*---滚动条大小--*/ }
    .main-article .article-layout ::-webkit-scrollbar {
      width: 7px;
      height: 7px; }
    .main-article .article-layout .article-slider {
      background-color: #fff;
      overflow: auto;
      border-right: 1px solid #d8d8d8; }
      .main-article .article-layout .article-slider ul {
        border: 0;
        overflow-x: hidden; }
    .main-article .article-layout .article-content {
      background: #fff;
      padding: 0 40px; }
  .main-article .anchor-area {
    position: fixed;
    top: 40vh;
    right: 10%; }
    .main-article .anchor-area .ant-anchor-wrapper {
      background-color: inherit; }
